  --color-day: #FDCE6D; /* Cor de fundo das células */
  --box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3); /* Sombra das células */
}

/* ... Outras variáveis ... */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #ffffff;

}
#calendar{
  background-color: #ffffff; 
  margin: 40px auto;
  padding: 0 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Sombra */
  display: flex;
  width: 900px;
  
  
}
/* Mudar a cor de fundo dos dias do calendário */

.fc .fc-daygrid-day {
  background-color: #ADD8E6; /* azul claro */
  border-radius: 10%; /* bordas arredondadas */
  height: 100px; /* ajuste a altura conforme necessário */
  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
} /* substitua com o código exato da cor que você quer */
.fc .fc-col-header-cell {
  background-color: #2F435A; /* azul mais escuro */
  height: 40px; /* ajuste a altura conforme necessário */
  border-radius: 5px; /* bordas arredondadas */
  text-transform: uppercase; /* transforma o texto em maiúsculas */
  color: #FDCE6D; /* muda a cor do texto para amarelo */
  font-weight: 300; /* torna a fonte mais fina; pode usar 400 para 'normal' e 700 para 'bold' */
  text-align: center; /* centraliza o texto */
  text-transform: uppercase; /* mantém o texto em maiúsculas */
  
}
.fc .fc-col-header-cell-cushion {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 300; /* texto mais fino */
  text-transform: uppercase; /* texto em maiúsculas */
  color: #FDCE6D; /* cor do texto amarela */
  height: 40px;
}


/* Mudar a altura dos dias do calendário */
.fc .fc-daygrid-day {
  height: 100px; /* ajuste conforme necessário */
}

#searchButton {
  background-color: rgba(255, 231, 190, 0.79);
  border: none;
  border-radius: 4px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  padding: 10px; /* ajustado para ter uma aparência uniforme */
  cursor: pointer;
  padding: 10px 20px;
  margin: 10px auto;
  display: block;
  cursor: pointer;
}
#popup-container {
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  max-width: 400px;
  background-color: rgba(240, 223, 186, 0.97);
  padding: 20px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  display: none; /* Escondido por padrão */
  border-radius: 10px;
  

}

#popup-container h2 {
  color: #000000;
}

#popup-container p {
  color: #000000;
  margin: 10px;
  font-weight: 400;
}
.pop-button {
  background-color: rgba(239, 169, 49, 0.79);
  border: none;
  border-radius: 4px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  padding: 10px; /* ajustado para ter uma aparência uniforme */
  cursor: pointer;
  padding: 10px 20px;
  margin: 10px auto;
  display: block;
  cursor: pointer;
  font-weight: bold;
  
}


.event-name {
  font-size: 16px;
  font-weight: bold;
}

#menu {
    
  width: 10%;
  height: 100vh;
  background-color: rgba(253, 206, 109, 1); 
  float: left;
  color: #2F435A; 
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}



ul {
  list-style-type: none;
  padding: 0; 
  margin: 0; 
}

li {
  display: flex; 
  align-items: center; 
  padding: 15px 10px; 
  text-align: left; 
  cursor: pointer;
  transition: background-color 0.3s; 
  position: relative;
  font-size: 12px; 
}


  li:hover {
      background-color: rgba(253, 206, 109, 1); 
  }

.menu-icon {
  width: 20px;
   padding: 3px  5px;
  margin-right: 10px;
}

ul ul {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  background-color: #FFF1D4; 
  width: 180px; 
}

li:hover > ul {
  display: block;
}
.geral {
  display: flex; 
  flex-direction: column; 
  align-items: center;
  height: 80%;
}

.titulo {
  font-size: 16px;
  text-align: center; 
  margin-bottom: 5px;
  font-weight: bold;
  margin-top: 10px;
  line-height: 1.2; 
  text-align: center;
  color: #2F435A;
  text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}
/* CLIENTE */
.label-title-cliente {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  margin-top: 40px;
  width: 700px;
  height: 30px;
  border-radius: 50px;
  align-items: center;
  background-color: #2F435A;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.text-title-cliente {
  display: flex;
  height: 20px;
  padding-top: 1px;
  margin-left: 10px;
  align-items: center;
  color: #FDCE6D;
  font-family: poppinssemibold, sans-serif;
  font-size: 20px;
}


/*--> id e nome do cliente <--*/
.input-line-1 {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: space-between;
  width: 42.5em;
}

.id-client-container {
  display: flex;
  margin-top: 10px;
  align-items: center;
  width: 100px;
  height: 30px;
  gap: 0.15625em;
}

.fa-id-card {
  color: #2F435A;
  font-size: 26px;
  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.id-client-container input {
  color: #FDCE6D;
}

#idClient {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1px;
  padding-left: 8px;
  width: 80px;
  height: 26px;
  color: rgba(47, 67, 90, 0.5);
  font-family: poppinsmedium, sans-serif;
  font-size: 16px;
  border: none;
  background-color: rgba(255, 231, 190, 0.79);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  outline: 0;
}

.name-client-container {
  display: flex;
  margin-left: 16px;
  margin-top: 10px;
  align-items: center;
  width: 480px;
  height: 30px;
  gap: 0.15625em;
}

.fa-user {
  color: #2F435A;
  font-size: 24px;
  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.name-client-container input {
  color: #FDCE6D;
}

#nameClient {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 8px;
  width: 530px;
  height: 30px;
  color: rgb(47, 67, 90);
  font-family: poppinsmedium, sans-serif;
  font-size: 16px;
  border: none;
  background-color: rgba(255, 231, 190, 0.79);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  outline: 0;
}

/*--> id e nome do cliente <--*/


/*--> email do cliente <--*/
.email-client-container {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  align-items: center;
  width: 42.5em;
  height: 30px;
  gap: 0.15625em;
}

.fa-envelope {
  color: #2F435A;
  font-size: 1.78125em;
  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.email-client-container input {
  color: #FDCE6D;
  height: 30px;
}

#emailClient {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 0.08em;
  padding-top: 1px;
  padding-left: 8px;
  width: 644px;
  height: 30px;
  color: rgb(47, 67, 90);
  font-family: poppinsmedium, sans-serif;
  font-size: 16px;
  border: none;
  background-color: rgba(255, 231, 190, 0.79);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  outline: 0;
}


/*--> telefone do cliente <--*/
.fone-client-container {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  align-items: center;
  width: 42.5em;
  height: 30px;
  gap: 0.15625em;
}

.fa-phone {
  color: #2F435A;
  font-size: 1.7em;
  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.fone-client-container input {
  color: #FDCE6D;
}

#foneClient {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3px;
  padding-left: 8px;
  width: 644px;
  height: 30px;
  color: rgb(47, 67, 90);
  font-family: poppinsmedium, sans-serif;
  font-size: 16px;
  border: none;
  background-color: rgba(255, 231, 190, 0.79);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  outline: 0;
}







